import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import nodeLogo from './nodejs-logo.png';
import MotionMenu from 'react-motion-menu';
import classNames from 'classnames'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {animation: false, clicked: false};
  }

  handleClick() {
    if(this.state.clicked){
      this.setState({animation: true, clicked: false})
    }
    else{
      this.setState({animation: false, clicked: true})
    }
  }

  render() {
    const buttonClasses = classNames({
      'button': true,
      'animation': this.state.animation
    });
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to React</h2>
        </div>
        <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p>
        <MotionMenu
          type="circle"
          margin={120}
          x={400}
          y={200}
          bumpy={false}
          openSpeed={250}
        >
          <div onClickCapture={this.handleClick.bind(this)} className={buttonClasses}>
            <i  className="fa fa-bars" />
          </div>
          <div className='button'>
            <img height={70} src={nodeLogo} />
          </div>
          <div className="button">
            <i className="fa fa-cloud" />
          </div>
          <div className="button">
            <i className="fa fa-home" />
          </div>
          <div className="button">
            <i className="fa fa-home" />
          </div>
        </MotionMenu>
      </div>
    );
  }
}




export default App;



